import Item from './Item';
import cloneDeep from 'lodash/cloneDeep';
import { INodeItem, IDrawPartVm } from '.';
export default class NodeItem extends Item implements INodeItem {
  public x: number;
  public y: number;
  public nodeType: string;
  public dom?: HTMLElement; // Âè™Ë¶ÅÈÄâÊã©ËøáÊ≠§ËäÇÁÇπ, Â∞±‰ºöË¢´ËÆ∞ÂΩï, Âú®ËäÇÁÇπblurÊó∂, ÂèØ‰ª•Âè™ÈÅçÂéÜËøô‰∫õË¢´ÈÄâÊã©ËøáÁöÑ, ‰ΩøÂÖ∂ÂèñÊ∂àÈÄâÊã©Âç≥ÂèØ

  constructor(vm: IDrawPartVm, nodeInfo: INodeItem) {
    super(vm, nodeInfo);
    // 1. ËäÇÁÇπÂü∫Êú¨ÂèÇÊï∞
    this.x = nodeInfo.x;
    this.y = nodeInfo.y;
    this.nodeType = nodeInfo.nodeType;
    // 2. ÂàùÂßãÂåñÂõæÂΩ¢
    setTimeout(() => {
      // 2.1 ËÆæÁΩÆÂõæÂΩ¢ÂèÇÊï∞
      this.setDefaultCfg();
      // 2.2 ËÆæÁΩÆËäÇÁÇπÂèØÊãñÂä®
      this.setDraggable();
    }, 0);
    const cloneNodeInfo = cloneDeep(nodeInfo);
    Object.assign(this, cloneNodeInfo);
  }

  /**
   * Êõ¥Êñ∞‰∏öÂä°Êï∞ÊçÆ
   * @param task ‰ªªÂä°
   */
  public updateTask(task: object) {
    this.task = cloneDeep(task);
  }

  public setSelected(flag: boolean, dom: HTMLElement) {
    this.dom = dom;
    this.vm.$set(this, 'selected', flag);
    if (flag) {
      dom.classList.add('node-selected');
    } else {
      dom.classList.remove('node-selected');
    }
  }

  /**
   * ËÆæÁΩÆËäÇÁÇπÈªòËÆ§jsplumbÈÖçÁΩÆ
   */
  private setDefaultCfg(): void {
    // ËÆæÁΩÆÈªòËÆ§ËøûÁ∫øÊ†∑ÂºèÂèäoverlays
    const common = {
      allowLoopback: false, // ‰∏çÂèØÂõûÁéØ
    };
    const chart = this.getJsplumbInstance();
    // todo ËøôÈáåÂèØ‰ª•‰ªéÂ§ñÈÉ®‰º†ÂÖ•ËÆæÁΩÆ
    // **Ê∑ªÂä†Ëµ∑ÂßãÁÇπ**
    const outPoint = chart.addEndpoint(this.id, {
      uuid: 'out-' + this.id,
      isSource: true,
      // @ts-ignore Êúâ!
      anchors: [
        // 'Right', 'Top', 'Left', 'Bottom'
        'Right',
      ],
      endpoint: ['Dot', {
        radius: 6,
        cssClass: 'end-point',
        hoverClass: 'end-point-hover'
      }]
    }, common);
    // **Ê∑ªÂä†ÁªìÊùüÁÇπ**
    const inPoint = chart.addEndpoint(this.id, {
      uuid: 'in-' + this.id,
      isTarget: true,
      // @ts-ignore Êúâ!
      anchors: [
        // 'Left', 'Top', 'Right', 'Bottom'
        'Left',
      ],
      endpoint: ['Rectangle', {
        width: 10,
        height: 10,
        cssClass: 'end-point',
        hoverClass: 'end-point-hover',
      }],
    }, common);
  }

  /**
   * ËÆæÁΩÆËäÇÁÇπÂèØÊãñÂä®
   */
  private setDraggable(): void {
    const chart = this.getJsplumbInstance();
    chart.draggable(this.id, {
      // grid: [4, 4],
      // start: e => {
      //   console.log('start')
      // },
      // ËäÇÁÇπÊãñÂä®ÁªìÊùü‰ΩçÁΩÆËÆ∞ÂΩï
      stop: (e: { pos: [number, number] }) => {
        const [x, y] = e.pos;
        this.x = x;
        this.y = y;
      },
    });
  }
}

export function createNode(vm: IDrawPartVm, nodeInfo: INodeItem): NodeItem {
  const node = new NodeItem(vm, nodeInfo);
  vm.nodes.push(node);
  return node;
}
